body {
  margin: 0;
  padding: 0;
  background-color: #f5699d;
  animation: bgChange 3s infinite alternate;
  /* 무한반복(infinite), 동작교차(alternate) */
}

@keyframes bgChange {
  0% {
    background-color: #f5699d;
  }
  50% {
    background-color: #fff;
  }
  100% {
    background-color: #f5699d;
  }
}
.center-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.center-container a img {
  min-width: 100%;
}